"use client"

import * as React from "react"
import { motion } from "framer-motion"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import Link from "next/link"

interface HeroProps extends Omit<React.HTMLAttributes<HTMLElement>, 'title'> {
  gradient?: boolean
  blur?: boolean
  title: React.ReactNode
  subtitle?: React.ReactNode
  actions?: {
    label: string
    href: string
    variant?: "default" | "destructive" | "outline" | "secondary" | "ghost" | "link"
  }[]
  titleClassName?: string
  subtitleClassName?: string
  actionsClassName?: string
}

const Hero = React.forwardRef<HTMLElement, HeroProps>(
  (
    {
      className,
      gradient = true,
      blur = true,
      title,
      subtitle,
      actions,
      titleClassName,
      subtitleClassName,
      actionsClassName,
      ...props
    },
    ref,
  ) => {
    return (
      <section
        ref={ref}
        className={cn(
          "relative z-0 flex min-h-[80vh] w-full flex-col items-center justify-center overflow-hidden rounded-md bg-background",
          className,
        )}
        {...props}
      >
        {gradient && (
          <div className="absolute top-0 isolate z-0 flex w-screen flex-1 items-start justify-center">
            {blur && (
              <div className="absolute top-0 z-50 h-96 w-screen bg-transparent opacity-20 backdrop-blur-xl" />
            )}

            {/* Main continuous glow effect */}
            <motion.div
              initial={{ scale: 0.9, opacity: 0.7 }}
              animate={{ 
                scale: [0.9, 1.05, 0.95, 1.02, 0.9],
                opacity: [0.7, 0.9, 0.8, 0.85, 0.7],
              }}
              transition={{
                duration: 15,
                repeat: Infinity,
                ease: "easeInOut",
              }}
              className="absolute inset-auto z-40 h-[40rem] w-[140rem] -translate-y-[60%] rounded-[50%] bg-gradient-radial from-blue-400/80 via-blue-500/40 to-transparent opacity-80 blur-[80px]"
            />
            
            {/* Secondary pulsing glow */}
            <motion.div
              initial={{ scale: 1, opacity: 0.6 }}
              animate={{ 
                scale: [1, 1.1, 0.95, 1.05, 1],
                opacity: [0.6, 0.8, 0.5, 0.7, 0.6],
              }}
              transition={{
                duration: 12,
                repeat: Infinity,
                ease: "easeInOut",
                repeatType: "reverse",
              }}
              className="absolute inset-auto z-30 h-[45rem] w-[130rem] -translate-y-[40%] rounded-[40%] bg-gradient-radial from-blue-500/70 via-blue-600/30 to-transparent opacity-70 blur-[120px]"
            />

            {/* Horizontal flow effect */}
            <motion.div
              animate={{ 
                x: [-20, 20, -20],
                opacity: [0.5, 0.7, 0.5],
              }}
              transition={{
                duration: 18,
                repeat: Infinity,
                ease: "easeInOut",
              }}
              className="absolute inset-auto z-20 h-[50rem] w-[150rem] -translate-y-[30%] rounded-[60%] bg-gradient-radial from-blue-400/60 via-blue-500/30 to-transparent opacity-60 blur-[150px]"
            />

            {/* Left gradient flow */}
            <motion.div
              initial={{ opacity: 0.5, width: "40rem" }}
              animate={{ 
                width: ["40rem", "70rem", "60rem", "75rem", "40rem"],
                opacity: [0.5, 0.9, 0.7, 0.8, 0.5],
              }}
              transition={{
                duration: 20,
                repeat: Infinity,
                ease: "easeInOut",
              }}
              style={{
                backgroundImage: `conic-gradient(var(--conic-position), var(--tw-gradient-stops))`,
              }}
              className="absolute inset-auto right-1/2 h-120 overflow-visible w-[60rem] bg-gradient-conic from-blue-500/80 via-blue-400/40 to-transparent [--conic-position:from_70deg_at_center_top]"
            >
              <div className="absolute w-[100%] left-0 bg-background h-60 bottom-0 z-20 [mask-image:linear-gradient(to_top,white,transparent)]" />
              <div className="absolute w-60 h-[100%] left-0 bg-background bottom-0 z-20 [mask-image:linear-gradient(to_right,white,transparent)]" />
              
              {/* Floating particles on left side - synchronized with the flow */}
              <motion.div
                animate={{
                  y: [0, -40, -20, -50, 0],
                  opacity: [0.3, 0.7, 0.5, 0.6, 0.3],
                }}
                transition={{
                  duration: 15,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="absolute top-1/3 left-1/4 h-7 w-7 rounded-full bg-blue-200 blur-[3px] z-30"
              />
              <motion.div
                animate={{
                  y: [0, -45, -25, -55, 0],
                  x: [0, 22, 10, 18, 0],
                  opacity: [0.2, 0.6, 0.4, 0.5, 0.2],
                }}
                transition={{
                  duration: 18,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="absolute top-1/2 left-1/3 h-9 w-9 rounded-full bg-blue-300/80 blur-[4px] z-30"
              />
              <motion.div
                animate={{
                  y: [0, -60, -30, -45, 0],
                  x: [0, -15, -5, -20, 0],
                  opacity: [0.2, 0.5, 0.3, 0.4, 0.2],
                }}
                transition={{
                  duration: 15,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="absolute top-2/3 left-1/5 h-5 w-5 rounded-full bg-blue-200/90 blur-[3px] z-30"
              />
            </motion.div>

            {/* Right gradient flow */}
            <motion.div
              initial={{ opacity: 0.5, width: "30rem" }}
              animate={{ 
                width: ["30rem", "60rem", "50rem", "65rem", "30rem"],
                opacity: [0.5, 0.9, 0.7, 0.8, 0.5],
              }}
              transition={{
                duration: 20,
                repeat: Infinity,
                ease: "easeInOut",
                delay: 2,
              }}
              style={{
                backgroundImage: `conic-gradient(var(--conic-position), var(--tw-gradient-stops))`,
              }}
              className="absolute inset-auto left-1/2 h-120 w-[60rem] bg-gradient-conic from-transparent via-blue-400/40 to-blue-500/80 [--conic-position:from_290deg_at_center_top]"
            >
              <div className="absolute w-60 h-[100%] right-0 bg-background bottom-0 z-20 [mask-image:linear-gradient(to_left,white,transparent)]" />
              <div className="absolute w-[100%] right-0 bg-background h-60 bottom-0 z-20 [mask-image:linear-gradient(to_top,white,transparent)]" />
              
              {/* Floating particles on right side - synchronized */}
              <motion.div
                animate={{
                  y: [0, -40, -20, -35, 0],
                  opacity: [0.2, 0.6, 0.4, 0.5, 0.2],
                }}
                transition={{
                  duration: 16,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="absolute top-1/3 right-1/4 h-6 w-6 rounded-full bg-blue-200/90 blur-[3px] z-30"
              />
              <motion.div
                animate={{
                  y: [0, -55, -30, -40, 0],
                  x: [0, -22, -10, -15, 0],
                  opacity: [0.3, 0.7, 0.5, 0.6, 0.3],
                }}
                transition={{
                  duration: 18,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 1,
                }}
                className="absolute top-1/2 right-1/3 h-8 w-8 rounded-full bg-blue-300/80 blur-[4px] z-30"
              />
              <motion.div
                animate={{
                  y: [0, -35, -20, -45, 0],
                  x: [0, -12, -5, -15, 0],
                  opacity: [0.2, 0.5, 0.4, 0.3, 0.2],
                }}
                transition={{
                  duration: 17,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="absolute top-2/5 right-1/5 h-12 w-12 rounded-full bg-blue-300/60 blur-[7px] z-30"
              />
            </motion.div>
            
            {/* Central flowing effect */}
            <motion.div
              animate={{ 
                y: [-30, -60, -40, -70, -30],
                scale: [0.9, 1.1, 1, 1.05, 0.9],
                opacity: [0.3, 0.7, 0.5, 0.6, 0.3],
              }}
              transition={{
                duration: 20,
                repeat: Infinity,
                ease: "easeInOut",
              }}
              className="absolute top-1/3 inset-x-[40%] h-16 w-16 rounded-full bg-blue-300/80 blur-[10px] z-30"
            />
            <motion.div
              animate={{ 
                y: [-40, -70, -50, -60, -40],
                x: [-10, 20, 0, 10, -10],
                scale: [1, 1.2, 0.9, 1.1, 1],
                opacity: [0.2, 0.6, 0.4, 0.5, 0.2],
              }}
              transition={{
                duration: 18,
                repeat: Infinity,
                ease: "easeInOut",
                delay: 1,
              }}
              className="absolute top-1/4 inset-x-[60%] h-14 w-14 rounded-full bg-blue-400/60 blur-[8px] z-30"
            />
            
            {/* Connecting light beam - animating */}
            <motion.div
              initial={{ scaleX: 0.8, opacity: 0.5, width: "75%" }}
              animate={{ 
                scaleX: [0.8, 1.1, 0.9, 1, 0.8],
                opacity: [0.5, 0.8, 0.6, 0.7, 0.5],
                width: ["75%", "85%", "80%", "90%", "75%"]
              }}
              transition={{
                duration: 15,
                repeat: Infinity,
                ease: "easeInOut",
              }}
              className="absolute inset-auto z-20 top-[20%] h-[3px] bg-blue-300/70 shadow-[0_0_15px_5px_rgba(147,197,253,0.5)]"
            />
          </div>
        )}

        <motion.div
          initial={{ y: 100, opacity: 0.5 }}
          viewport={{ once: true }}
          transition={{ ease: "easeInOut", delay: 0.3, duration: 0.8 }}
          whileInView={{ y: 0, opacity: 1 }}
          className="relative z-50 container flex justify-center flex-1 flex-col px-5 md:px-10 gap-4 -translate-y-20"
        >
          <div className="flex flex-col items-center text-center space-y-4">
            <h1
              className={cn(
                "text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold tracking-tight",
                titleClassName,
              )}
            >
              {title}
            </h1>
            {subtitle && (
              <p
                className={cn(
                  "text-xl text-muted-foreground",
                  subtitleClassName,
                )}
              >
                {subtitle}
              </p>
            )}
            {actions && actions.length > 0 && (
              <div className={cn("flex gap-4", actionsClassName)}>
                {actions.map((action, index) => (
                  <Button
                    key={index}
                    variant={action.variant || "default"}
                    asChild
                  >
                    <Link href={action.href}>{action.label}</Link>
                  </Button>
                ))}
              </div>
            )}
          </div>
        </motion.div>
      </section>
    )
  },
)
Hero.displayName = "Hero"

// Default export that creates a simple Hero with just the animation background
// This maintains compatibility with existing usage of HeroAnimation
export default function HeroAnimation() {
  return (
    <Hero 
      title=""
      className="min-h-[90vh] h-full"
      titleClassName="sr-only"
    />
  )
}
export { Hero } 
